<template>
  <div class="mainXiaoGuo">
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Find Store</h1>
      <div class="chartWrap"></div>
    </section>
    <div id="map_canvas" style="height: 100vh; width: 100%"></div>
  </div>
</template>

<script>
import { nearbyStore } from "@/api/store";
import {bindStore} from '@/api/user';
export default {
  name: "Map",
  data() {
    return {
      value: "",
      show: false,
      center: { lng: 120.5, lat: 30.2 },
      position: {},
      storeList: [],
      infowindows:[],
      websock: null,
      type:this.$route.query.type||0,
      map: {},
      useTel:this.$route.query.param ? this.$route.query.param.useTel:true,
      tel: this.$route.query.param ? this.$route.query.param.tel : "",
      nickname: this.$route.query.param ? this.$route.query.param.nickname : "",
      sex: this.$route.query.param ? (this.$route.query.param.sex ? this.$route.query.param.sex : "Man") : "Man",
      email: this.$route.query.param ? this.$route.query.param.email : "",
      registerPwd: this.$route.query.param ? this.$route.query.param.registerPwd : "",
    };
  },
  created() {},
  beforeDestroy() {
    if(this.intervalId){
      clearInterval(this.intervalId);
    }
  },
  destroyed: function() {},
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
       let isLoad = false;
      setTimeout(() => {
        if (isLoad) {
          return false;
        }
        let res = {
        longitude: 114.406149,
        latitude: 30.476756
      };
        this.center = { lng: res.longitude, lat: res.latitude };
        nearbyStore({
          countryCode: "ES",
          longitude: res.longitude,
          latitude: res.latitude
        }).then(res => {
          this.storeList = res.data.result;
           try{
            this.mapBuild();
          }catch(e){
            this.$toast(e);
          }
        });
      }, 2000);
        this.$store.dispatch("getUserInfo").then(res => {
        isLoad=true;
        this.center = { lng: res.longitude*1, lat: res.latitude*1 };
        nearbyStore({
          countryCode: "ES",
          longitude: res.longitude*1,
          latitude: res.latitude*1
        }).then(res => {
          this.storeList = res.data.result;
          try{
            this.mapBuild();
          }catch(e){
            this.$toast(e);
          }
        });
      });
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.bus.$emit('goBack');;
    },
    //  地图实例
    mapBuild() {
      console.log(this.center);
      //创建地图实例，zoom是缩放比例
      // this.map = new google.maps.Map(document.getElementById("map_canvas"), {
      //   zoom: 12,
      //   center: this.center,
      //   mapTypeId: google.maps.MapTypeId.ROADMAP,
      //   gestureHandling: "greedy"
      // });
      //创建地图实例，zoom是缩放比例
      this.map = new google.maps.Map(document.getElementById("map_canvas"), {
        zoom: 12,
        center: this.center,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
        gestureHandling: "greedy"
      });

      let contentString =
        '<div id="content">' +
        '<div id="siteNotice">' +
        "</div>" +
        '<div class="storeWrap">' +
        '<div class="storeNews">' +
        '<div class="storePhoto">' +
        '<img src="{img}">' +
        "</div>" +
        '<div class="right_name">' +
        "<p>{storeName}</p>" +
        '<div class="address">' +
        '<span class="iconfont red"> &#xe601; </span>' +
        "<div>{street}</div>" +
        "</div>" +
        "</div>" +
        "</div>" +
        '<div class="number">' +
        '<div class="fansNo">Fans NO.<font>{fansNum}</font></div>' +
        '<div class="orderNo">Order NO.<font>{orderNum}</font></div>' +
        "</div>" +
        '<div class="btnWrap">' +
        '<div class="anotherOne anotherOne_{index}" data-index="{index}" >Another One</div>' +
        '<div class="gotIt gotIt_{index}" data-index="{index}" >Got It</div>' +
        "</div>" +
        "</div>" +
        "</div>" +
        "</div>" +
        "</div>";

      let marker,
        t = this;

      //这里因为需要很多标记，所以遍历了this.markers（后端给的数据）来循环创建标记
      this.storeList.forEach((item, i) => {
        let index = i;
        item.index = index;
        let infowindow = new google.maps.InfoWindow({
          content: contentString.format(item)
        });
        console.log(index, item, infowindow.content);

        marker = new google.maps.Marker({
          position: { lng: item.longitude * 1, lat: item.latitude * 1 },
          // icon: item.img?item.img:"../../../static/img/qishou.png", //标记自定义图标
          draggable: false, //不可拖动
          map: this.map, //地图实例
          gestureHandling: "greedy"
          // labelContent: item.storeName, //label的内容
          // labelAnchor: new google.maps.Point(22, 0), //label的位置，可以调
          // labelClass: "labels", // the CSS class for the label
          // labelStyle: { background: "#fff", padding: "5px" }
        });

        marker.addListener("click", function() {
          infowindow.open(this.map, marker);
        });
        t.infowindows.push({infowindow,marker});
        if(i==0){
           infowindow.open(this.map, marker);
        }
      });

      let handlerAnotherOne=()=>{
         let el = document.querySelector(".anotherOne");
         let index=el.attributes['data-index'].value*1;
         t.infowindows[index].infowindow.close(); //this.map,t.infowindows[index].marker
         if(t.infowindows.length>1){
           t.infowindows[index==t.infowindows.length-1?0:index+1].infowindow.open(t.map, t.infowindows[index==t.infowindows.length-1?0:index+1].marker);
         }

      };
      let handlerGotIt=()=>{
         let el = document.querySelector(".gotIt");
        let index=el.attributes['data-index'].value*1;
        let store=t.storeList[index];
        if(t.type==0){
            let queryParam = {
                invitationCode:store.registCode,
                registerPwd: this.registerPwd,
                tel: this.tel,
                nickname: this.nickname,
                sex: this.sex,
                email: this.email,
                useTel:this.useTel,
            }
            t.$router.push({path:'login',query:{param:queryParam}
                
            });
        }else if(t.type==1){
            t.$router.push({path:'fillInvitation',query:{invitationCode:store.registCode}});
        }else if(t.type==2){
            t.$store.dispatch('changeLocation',JSON.stringify(store));
            t.$router.go(-1);
        }else{
           bindStore({
            storeId:store.id
        }).then(res=>{
          t.$router.push({path:'/',query:{}});
        });
        }
      }

      let a = () => {
        if (!document.querySelector(".gotIt")) {
          return false;
        }
        if (document.querySelector(".gotIt")) {
          let gotIt = document.querySelector(".gotIt");
          if (gotIt.isBind) {
            return false;
          }
          google.maps.event.addDomListener(gotIt, "click", handlerGotIt);

          google.maps.event.addDomListener(
            document.querySelector(".anotherOne"),
            "click",
            handlerAnotherOne
          );
          gotIt.isBind = true;
        }
      };

      this.intervalId=setInterval(a,100);
      
    }
  }
};
</script>
<style lang="scss" scoped>
#map_canvas /deep/ .storeWrap {
  width: 273px;
  height: 177px;
  .storeNews {
    width: 100%;
    display: -webkit-box;
    padding-top: 20px;
    .storePhoto {
      width: 57px;
      height: 57px;
      border-radius: 50%;
      overflow: hidden;
      border: none;
      margin-right: 18px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .right_name {
      width: 198px;
      padding-top: 10px;
      p {
        font-size: 14px;
        color: #333333;
        padding-bottom: 8px;
      }
      .address {
        display: -webkit-box;
        div {
          font-size: 13px;
          color: #999999;
          width: 180px;
          word-wrap: break-word;
          word-break: break-all;
          overflow: hidden;
          padding-left: 5px;
        }
      }
    }
  }
  .number {
    width: 100%;
    display: -webkit-box;
    padding-top: 10px;
    div {
      height: 20px;
      width: 50%;
      text-align: center;
      line-height: 20px;
    }
    .fansNo {
      color: #666666;
      font-size: 11px;
      border-right: 1px solid #999999;
      font {
        color: #111111;
        font-size: 13px;
      }
    }
    .orderNo {
      color: #666666;
      font-size: 11px;
      font {
        color: #111111;
        font-size: 13px;
      }
    }
  }
  .btnWrap {
    height: 32px;
    margin-top: 15px;
    width: 100%;
    display: flex;
    justify-content: center;
    .anotherOne {
      border: 1px solid #e33946;
      font-size: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100px;
      margin: 0 15px;
      color: #e33946;
    }
    .gotIt {
      border: 1px solid #e33946;
      height: 100%;
      width: 100px;
      background: #e33946;
      font-size: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100px;
      margin: 0 15px;
      color: #ffffff;
    }
  }
}
#map_canvas /deep/ .gm-style .gm-style-iw-c {
  max-width: 303px !important;
}
#map_canvas /deep/ .gm-style .gm-style-iw-d {
  max-width: 303px !important;
}
</style>


